
import { defineComponent, onMounted, ref } from "vue";
import ApiService from "@/core/services/ApiService";
import { setCurrentPageBreadcrumbs } from "@/core/helpers/breadcrumb";
import PortSelect from "@/components/website/selects/PortSelect.vue";
import RateResponse from "@/components/website/RateResponse.vue";
import RateNotFoundResponse from "@/components/website/RateNotFoundResponse.vue";

export default defineComponent({
  name: "Rate",
  components: { PortSelect, RateResponse, RateNotFoundResponse },
  setup() {
    const loading = ref(false);
    const deliver = ref("");
    const pol = ref("");
    const pod = ref("");
    const reception = ref("");

    const name = ref("");
    const email = ref("");
    const cif = ref("");
    const mobile = ref("");
    const addres = ref("");

    const kind_containers = ref<[] | any>([]);
    const kind_containers_selected = ref<[] | any>([]);
    const quotations = ref<[] | any>([]);
    const nodata = ref<[] | any>([]);

    const getKindContainers = () => {
      ApiService.get("/api/kind_container").then(({ data }) => {
        data.forEach(function (value, index) {
          if (value.web_description !== "" && value.web_description !== null) {
            kind_containers.value.push(value);
          }
        });
      });
    };

    const updateKindContainersSelected = (item) => {
      let addFlag = true;
      kind_containers_selected.value.forEach(function (value, index) {
        if (value === item.code && addFlag) {
          kind_containers_selected.value.splice(index, 1);
          addFlag = false;
        }
      });
      if (addFlag) {
        kind_containers_selected.value.push(item.code);
      }
    };

    const getQuotations = () => {
      loading.value = true;
      ApiService.query("/api/lima/quotations", {
        params: {
          qt2polcde: pol.value,
          qt2podcde: pod.value,
          qt2kopcde: kind_containers_selected.value,
        },
      }).then(({ data }) => {
        quotations.value = data.tarif;
        nodata.value = data.not_found;
        kind_containers_selected.value = [];
        deliver.value = "";
        pol.value = "";
        pod.value = "";
        reception.value = "";
        name.value = "";
        email.value = "";
        cif.value = "";
        mobile.value = "";
        addres.value = "";
        loading.value = false;
      });
    };

    onMounted(() => {
      getKindContainers();
    });

    return {
      kind_containers,
      kind_containers_selected,
      quotations,
      nodata,
      loading,
      deliver,
      pol,
      pod,
      reception,
      name,
      email,
      cif,
      mobile,
      addres,
      getKindContainers,
      updateKindContainersSelected,
      getQuotations,
    };
  },
});
